<template>
    <div class="classif-wrap">
        <div v-title data-title="明日预告">明日预告</div>
        <header  class="header">明日预告</header>
        <div class="hotGoods-wrap">
            <div class="sold">
                <div class="sold_time">
                    <ul class="clearfix" :style="{width:(stime.length+1)*1.5+'rem'}">
                        <li class="fl" v-for="(item,index) in stime" :key="item.id" :class="{active:status[index]}" @click="tab(item,index)">{{item.name}}</li>
                    </ul>
                </div>
            </div>
            <ul class="content-main clearfix">
                <li class="fl" v-for="(item,index) in data" :key="item.id">
                    <div class="image"  @click="tolink(index)">
                        <img v-if="item.pic_url" v-lazy="URL + item.pic_url">
                        <img v-if="item.stock == 0" class="sellout_img" src="../../assets/sellout2.jpg" alt="">
                    </div>
                    <div style="height:1.2rem">
                        <p class="desi">{{item.title}}</p>
                    </div>
                    <div class="price_market clearfix">
                        <p class="stock">库存：{{item.stock}}<strike class="market fr">￥{{item.price_market}}</strike></p>
                        <p class="price fl">￥<span>{{item.price_member}}</span></p>
                        <span class="love fr" :class="{active:item.is_collect}" @click="collect(index,item)"></span>
                    </div>
                </li>
            </ul>
            <div class="no_goods" v-show="show">暂无商品</div>
        </div> 
        <div class="load" v-show="load" @touchmove.prevent><mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner></div>
        <div class="load-wrap" v-show="$store.state.class_load" @touchmove.prevent><mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner></div>
    </div>
</template>
<script>
    import { Toast } from 'mint-ui';
    import QS from 'qs';
    export default {
        name : 'classification',
        data(){
            return {
                stime:'',
                status:[true,false],
                load:false,
                data:'',
                show:false,
                type:''
            }
        },
        components:{
          
        },
        methods:{
            // 点击时间段获取商品
            tab(item,index){
                for(var i = 0; i < this.status.length; i++){
                    this.$set(this.status,i,false);
                }
                this.$set(this.status,index,true);
                this.load = true;
                this.succ(item)
            },
            succ(item){
                this.axios({
                    url:API_URL + 'Home/Index/getSgoods',
                    method:'get',
                    params:{
                        cid:3,
                        sid:item.id,
                        app_user_id:sessionStorage.getItem('user_ID')
                    }
                }).then((suc) => {
                   if(suc.data.msg === '暂无商品'){
                        this.show = true;
                   }else{
                        this.show = false;
                   }
                    this.data = suc.data.data;
                    this.load = false;
                }).catch((err) => {
                    console.log(err);
                });
            },
            //收藏
            collect(index,item){
                if(!sessionStorage.getItem('user_ID')){ 
                    Toast('请先登录');
                    this.$router.push('/LogoIn');
                }
                if(item.is_collect == 0){
                    this.type = 1;
                    this.axios.post(API_URL + 'Home/Cart/add_collection',QS.stringify({
                        app_user_id:sessionStorage.getItem('user_ID'),
                        goods_id:item.id,
                        type:this.type
                    })).then((res) => {
                        if (res.data.msg == '收藏成功') {
                            this.data[index].is_collect = 1;
                        }else{
                            Toast({
                                message: res.data.msg,
                                position: 'middle',
                                duration: 800
                            });
                        }
                        Toast({
                            message: res.data.msg,
                            position: 'middle',
                            duration: 800
                        });
                    }).catch((err) => {
                        console.log(err);
                    })
                    
                }else{
                    this.type = 0;
                    this.axios.post(API_URL + 'Home/Cart/add_collection',QS.stringify({
                        app_user_id:sessionStorage.getItem('user_ID'),
                        goods_id:item.id,
                        type:this.type
                    })).then((res) => {
                        if (res.data.msg == '已取消') {
                            this.data[index].is_collect = 0;
                        }else{
                            Toast({
                                message: res.data.msg,
                                position: 'middle',
                                duration: 800
                            });
                        }
                        Toast({
                            message: res.data.msg,
                            position: 'middle',
                            duration: 800
                        });
                    }).catch((err) => {
                        console.log(err);
                    })
                }
            },
            // 商品详情
            tolink(index){
                this.$router.push({
                    name:'product',
                    params:{
                        id:this.data[index].id,
                        status:1
                    }
                });
            }
            
        },
        created() {
            //，明日抢购首次数据
            this.axios.post(API_URL+'Home/Index/tomorrow',QS.stringify({
                        app_user_id:sessionStorage.getItem('user_ID')
                    })).then((res) => {
                if(res.data.data.tomorrow_clear == ''){
                        this.show = true;
                   }else{
                        this.show = false;
                   }
                        this.$store.state.load_wrap = false;
                        this.banner = res.data.data.banner;
                        this.data = res.data.data.tomorrow_clear;
                        this.stime = res.data.data.soldname;
                    }).catch((err) => {
                        console.log(err);
                    })
        },
        mounted() {
            document.body.scrollTop = 0;
        },
        updated(){
            var that = this;
            setTimeout(function(){
                that.$store.state.class_load = false;
            },500);
        },
        destroyed(){
            this.scrollWatch = false;
        },
    }
</script>
<style lang="less" scoped>
    .classif-wrap{
        background:#f2f2f2;
        .header {
            background: #ed3851;
            text-align: center;
            color: #fff;
            font-size: .36rem;
            font-weight: 700;
            line-height: .62rem;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 1;
            width: 100%;
            height: .62rem;
            padding: .16rem 0;
        }
        .hotGoods-wrap{
            background:#f2f2f2;
            padding-top:.94rem;
            .sold {
                height:1rem;
                overflow:hidden;
                .sold_time {
                    background:#fff;
                    width:100%;
                    height:1.6rem;
                    overflow:hidden;
                    overflow-x:auto;
                    ul {
                        height:1.6rem;
                        li {
                            text-align:center;
                            width:1.45rem;
                            height:1rem;
                            line-height:1rem;
                            padding:0 .15rem;
                            font-size:.3rem;
                            color:#727171;
                            font-weight:500;
                        }
                        li.active{
                           color:#ed3851;
                        }
                    }
                }
            }
            
            .content-main{
                li{
                    width:3.7rem;
                    background:#fff;
                    margin-top:.1rem;
                    margin-right:.08rem;
                    .image{
                        width:3.7rem;
                        height:3.7rem;
                        img {
                            width:100%;
                            height:100%;
                        }
                        .sellout_img{
                            position:absolute;
                            width:2.5rem;
                            height:2.5rem;
                            top:17%;
                            left:17%;
                        }

                    }
                    .desi{
                        line-height:normal;
                        font-size:.3rem;
                        color:#333;
                        padding:.2rem;
                        overflow : hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        border-top:1px solid #f2f2f2;
                        box-sizing: border-box;
                        padding-bottom: 0;
                    }
                    .price_market {
                        padding:.2rem;
                        .stock {
                            font-size: .24rem;
                            color: #aeadad;
                            padding-bottom:.1rem;
                        }
                       .price{
                            line-height:normal;
                            color:#e02828;
                            font-size:.25rem;
                            span{
                                font-size:.36rem;
                            }
                        } 
                        .love {
                            width:.5rem;
                            height:.5rem;
                            background:url(../../assets/love.png) no-repeat;
                            background-size:100% 100%;
                        }
                        .love.active {
                            background:url(../../assets/love01.png) no-repeat;
                            background-size:100% 100%;
                        }
                    }
                    
                }
                li:nth-child(2n){
                    margin-right:0;
                }
            }
            .no_goods {
                height:70%;
                padding-top:1rem;
                color:#727171;
                font-size:.3rem;
                text-align:center;
                background:#fff;

            }
        }
        
    }
</style>


